<template>
  <div class="col-xl-6">
    <div
        class="main_cards"
    >
      <div>
        <h2
            class="intro-x font-bold text-2xl xl:text-3xl text-center xl:text-left"
        >
          Chempionat
        </h2>
        <form @submit.prevent="postForm()">
          <div class="intro-width mt-8 card">
            <input
                type="text"
                class="intro-x login__input form-control py-3 px-4 border-gray-300 "
                v-model="title"
                placeholder="Chepionat nomi"
            />
            <input type="file" class="login__input form-control py-3 px-4 border-gray-300 mt-3" id="cr_imagee"
                   multiple
                   @change="trySubmitFile1"
                   accept="image/*"
                   name="datafiless"
                   ref="datafiless"
            >
          </div>
          <button  class="btn my-4 btn-primary py-3 px-4 w-full xl:w-32 xl:mr-3 align-top" >Saqlash</button>
        </form>
      </div>
      </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      url:'',
      myImages:[],
      imageUrl:[],
      files_name:[],
      myImages1:[],
      imageUrl1:[],
      files_name1:[],
      id:'',
      title1:'',
      description1:'',
      title2:'',
      description2:'',
      title3:'',
      description3:'',
      title4:'',
      description4:''
    }
  },
  methods:{

    trySubmitFile1(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    postForm(){
      const form = new FormData();
      form.append("title", this.title);
      form.append("icon", this.myImages1[0]);
      this.$store.dispatch('main/postChampionship',form)

    },
  },

}
</script>
