    downloadPdf(){
      this.isLoading=true
      const el = this.$refs.printMe;
      const options = {
        type: 'dataURL',
        format: 'a4',
        useCORS: true,
        logging: true,
        allowTaint: false,

      };
      (async () => {
        this.output = await this.$html2canvas(el,  options);
        axios.post(`/diplomas/`,
            {
              "unique_code": this.items_main_id,
              "date":new Date(this.start_date).toLocaleDateString()+"-"+new Date(this.end_date).toLocaleDateString(),
              "first_style":this.logoStyle,
              "middle_style":this.logoStyle2,
              "last_style":this.logoStyle3,
              "sport_logo_style":this.style,
              "first_leader_style":this.styleText1,
              "second_leader_style":this.styleText2,
              "third_leader_style":this.styleText3,
              "textline_style":this.linesStyle,
              "championship":this.championships.id,
              "sport_type":this.sport_type.id,
              "diploma_category":this.diploma_danger.id,
              "first_text":this.text12,
              "middle_text":`<span><span class='sport_type'>${this.bold_text}</span> ${this.middle_text}</span>`,
              "last_text":this.text13

            }, {
              headers: {
                Authorization: `Bearer ${localStorage.getItem('token')}`,
              }
            })
            .then(res => {

              var organ=[
                {
                  "organization":this.orga_nization1.id,
                  "diploma":res.data.id
                },
                {
                  "organization":this.orga_nization2.id,
                  "diploma":res.data.id
                },
                {
                  "organization":this.orga_nization3.id,
                  "diploma":res.data.id
                },
              ]
                  axios.post('diploma-members/',organ,
                      {
                        headers: {
                          Authorization: `Bearer ${localStorage.getItem('token')}`,
                        }
                      })
              .then(()=>{
                var doc = new jsPDF('landscape','px',[3508, 2480])
                doc.setFontSize(10);
                doc.text('Stone', 10, 20);

                doc.addImage(this.output, 'PNG', 0, 0, 3508, 2480);
                doc.save('diplom.pdf')
                this.isLoading=false
                this.aler_diploma=true

                this.items_main_id= Math.floor(Math.random() * 10000000000) + 1
                this.value=`https://check.sport.uz/?code=${this.items_main_id}`;
              })

            })
            .catch(() => {
              this.isLoading=false
              this.aler_diploma=true
              this.text_alert="Yaratishda xatolik bor etiborliroq bo'ling"
            })

      })()
    },
