    pageCounts(){
      var counter=1,end_count=this.count_dip;
      var interval = setInterval(()=> {
        this.downloadPdf()
        if (counter <end_count) {
          this.text_alert=`${counter}-yuklash davom etilmoqda..`
          counter = counter + 1;
        }
        else {
          clearInterval(interval);
          this.text_alert=`Chop etish tugadi jami ${this.count_dip} yuklab olindi`
        }
      }, 3000);
    }
