<template>
  <div>
    <DarkModeSwitcher />
    <div class="container sm:px-10">
      <div class="block xl:grid grid-cols-2 gap-4">
        <!-- BEGIN: Login Info -->
        <div class="hidden xl:flex flex-col min-h-screen">
          <div class="my-auto">
<!--            <img-->
<!--              alt="Icewall Tailwind HTML Admin Template"-->
<!--              class="-intro-x w-1/2 -mt-16"-->
<!--              :src="require(`@/assets/images/logotype.svg`)"-->
<!--            />-->
            <div
              class="-intro-x text-white font-medium text-4xl leading-tight mt-10"
            >
              O‘ZBEKISTON RESPUBLIKASI<br> SPORTNI RIVOJLANTIRISH VAZIRLIGI
            </div>
            <div
              class="-intro-x mt-5 text-lg text-white text-opacity-70 dark:text-gray-500"
            >

            </div>
          </div>
        </div>
        <!-- END: Login Info -->
        <!-- BEGIN: Login Form -->
        <form @submit.prevent="postLogin()">
          <div class="h-screen xl:h-auto flex py-5 xl:py-0 my-10 xl:my-72">
            <div
                class="my-auto mx-auto xl:ml-20 bg-white dark:bg-dark-1 xl:bg-transparent px-5 sm:px-8 py-8 xl:p-0 rounded-md shadow-md xl:shadow-none w-full sm:w-3/4 lg:w-2/4 xl:w-auto"
            >
              <h2
                  class="intro-x font-bold text-2xl xl:text-3xl text-center xl:text-left"
              >
                Tizimga kirish
              </h2>
              <div class="intro-x mt-8">
                <input
                    type="text"
                    class="intro-x login__input form-control py-3 px-4 border-gray-300 block"
                    placeholder="Username"
                    v-model="username"
                />
                <input
                    type="password"
                    class="intro-x login__input form-control py-3 px-4 border-gray-300 block mt-10"
                    placeholder="Password"
                    v-model="password"
                />
              </div>
              <div class="login_btn intro-x mt-5 xl:mt-8 text-center xl:text-left">
                <button
                    class="btn btn-primary py-3 px-4 w-full xl:w-100 xl:mr-3 align-top xl:mt-4"
                >
                  <span v-if="!isLoading">Tizimga kirish</span>

                   <LoadingIcon v-if="isLoading" icon="oval" class="w-8 h-8 text-white" />
                </button>
              </div>
            </div>
          </div>
        </form>
        <!-- END: Login Form -->
      </div>
    </div>
  </div>
</template>

<script>

  import axios from "axios";
import { defineComponent, onMounted } from "vue";
export default defineComponent({
  data(){
    return{
      username:'',
      password:'',
      isLoading:false
    }
  },
  setup() {
    onMounted(() => {
      cash("body")
        .removeClass("main")
        .removeClass("error-page")
        .addClass("login");
    });
  },
  methods:{
    postLogin(){
      this.isLoading=true


    axios.post('/login/',{
      "username":this.username,
        "password":this.password
    })
        .then(res=>{
          localStorage.setItem("token",res.data.access)
          localStorage.setItem("username",res.data.username)
          this.$router.push('/')
        })
      .catch(()=>{
        this.isLoading=false
      })

  }
  }
});
</script>
<style>
  .login_btn svg{
    stroke: white
  }
</style>
